.profile-section {
            position: fixed;
            top: 9vh;
            left: 0;
            height: 80vh; /* Full viewport height */
            width: 250px; /* Adjust width as needed */
            padding: 20px;
            overflow-y: auto; /* Allows scrolling if content overflows */
        }

        /* Push main content right */
        .main-content {
            margin-left: 260px; /* Adjust based on profile width */
            padding: 20px;
        }
       
        /* Hide profile in mobile view */
        @media (max-width: 768px) {
            .profile-section {
                display: none;
            }
            .main-content {
                margin-left: 0;
            }
            .alert-container{
                display: hidden;
            }
            .alert{
                display: none;
            }
            
        }
       
       